body {
  background-color: #ccc;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  min-height: 97vh;
}

.heart {
  width: 150px;
  height: 150px;
  background-size: 100%;
  background-repeat: no-repeat;
  background-image:url("data:image/svg+xml;base64,");
}

.text {
     color: #ffffff;
     width: 150px;
}

.heart {
  -webkit-animation: HEARTBEAT 2.5s infinite;
          animation: HEARTBEAT 2.5s infinite;
}

.textAlgin {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.dear {
    font-size: 1.17em;
}

.fixMarginTop {
    margin-top: 20px;
}

@-webkit-keyframes HEARTBEAT {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  5% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }
  10% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  15% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes HEARTBEAT {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  5% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }
  10% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  15% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}



